@font-face {
  font-family: "Open sans";
  src: url("../fonts/opensans.woff2")
    format("woff2"),
    url("../fonts/opensans.woff")
    format("woff");

  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Open sans";
  src: url("../fonts/opensansbold.woff2")
    format("woff2"),
    url("../fonts/opensansbold.woff")
    format("woff");

  font-weight: bold;
  font-style: normal;
}
