@mixin head3 {
  margin: 0;
  padding: 30px 0 3px 0;

  font-size: 25px;
  line-height: 30px;
  text-align: left;

  color: #000000;
}

@mixin button {
  display: block;
  box-sizing: border-box;
  width: 260px;
  padding: 12px 0;
  margin: 0 auto;

  font-family: "Open sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;
  color: #444444;

  background-color: #ffffff;
  border: 2px solid #444444;

  cursor: pointer;

  &:hover {
    color: #ffffff;
    background-color: #444444;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

@mixin button-hover {
  display: block;
  box-sizing: border-box;
  width: 260px;
  padding: 12px 0;
  margin: 0 auto;

  font-family: "Open sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;
  color: #444444;

  background-color: #ffffff;
  border: 2px solid #444444;

  cursor: pointer;
}

@mixin center-mobile {
  width: 320px;
  margin: 0 auto;
}

@mixin center-tablet {
  width: 768px;
  margin: 0 auto;
}

@mixin center-desktop {
  width: 1150px;
  margin: 0 auto;
}
